import { removeClass, createHTMLElement } from '../../util';
const className = 'slider';

/**
 * Creates a slider when adding slider class to an ul element. The li elements are the slides.
 *
 * @param {className} slider - class name of Ul element containing li (slides).
 */

function initSlider(slider) {
    // slider items count
    let sliderChildren = [...slider.children];
    let sliderChildrenLength = sliderChildren.length;

    if (sliderChildrenLength < 2) {
        removeClass(slider, className, false);
        return;
    }

    for (const sliderChild of sliderChildren.entries()) {
        if (sliderChild[0] < 1) {
            sliderChild[1].classList.add('slider__active-slide');
        } else {
            // only displays first slide and hides rest
            sliderChild[1].classList.add('slider__slide');
        }
    }
    /* generates controls */
    let sliderControlsWrap = createHTMLElement('div', [
        { label: 'class', val: 'slider__controls__wrap' },
    ]);

    let sliderControls = createHTMLElement('div', [
        { label: 'class', val: 'slider__controls' },
    ]);

    let sliderProgress = createHTMLElement('div', [
        { label: 'class', val: 'slider__controls__progress' },
    ]);

    let sliderButtons = createHTMLElement('div', [
        { label: 'class', val: 'slider__controls__buttons' },
    ]);

    // generates progress
    sliderProgress.appendChild(
        createHTMLElement('span', [
            { label: 'content', val: '1' },
            {
                label: 'class',
                val: 'slide__controls__progress__active',
            },
        ])
    );
    sliderProgress.appendChild(
        createHTMLElement('span', [
            { label: 'content', val: ' /' },
            {
                label: 'class',
                val: 'slide__controls__progress__separator',
            },
        ])
    );
    sliderProgress.appendChild(
        createHTMLElement('span', [
            { label: 'content', val: sliderChildrenLength },
            { label: 'class', val: 'slide__controls__progress__total' },
        ])
    );

    sliderControlsWrap.appendChild(sliderControls).appendChild(sliderProgress);
    slider.appendChild(sliderControlsWrap);

    // generates buttons
    sliderButtons.appendChild(
        createHTMLElement('button', [
            { label: 'aria-label', val: 'Previous item' },
            { label: 'disabled', val: 'true' },
            {
                label: 'class',
                val:
                    'fas fa-arrow-left slider__controls__buttons__prev swiper-slider-arrow arrow-left--btn-prev',
            },
        ])
    );
    sliderButtons.appendChild(
        createHTMLElement('button', [
            { label: 'aria-label', val: 'Next item' },
            {
                label: 'class',
                val:
                    'fas fa-arrow-right slider__controls__buttons__next swiper-slider-arrow arrow-right--btn-next',
            },
        ])
    );

    sliderControls.appendChild(sliderButtons);
    slider
        .querySelector('.slider__controls__buttons__prev')
        .classList.add('slider__controls__buttons__disabled');

    // Adds event listener to buttons
    slider
        .querySelector('.slider__controls__buttons__prev')
        .addEventListener('click', function() {
            handleSlideChange(-1, slider, sliderChildrenLength, sliderChildren);
        });

    slider
        .querySelector('.slider__controls__buttons__next')
        .addEventListener('click', function() {
            handleSlideChange(1, slider, sliderChildrenLength, sliderChildren);
        });
}

/**
 * Handles direction of slide when sliding left or right
 *
 * @param {number} direction - either 1 (next) or -1 (prev)
 * @param {className} slider - class name of Ul element containing li (slides).
 * * @param {number} sliderChildrenLength - number of element children of class name above.
 * * @param {Element} sliderChildren - the slides / li  elements.
 */

function handleSlideChange(
    direction,
    slider,
    sliderChildrenLength,
    sliderChildren
) {
    // gets position of active slide
    let activeSlide = parseInt(
        slider.querySelector('.slide__controls__progress__active').innerText
    );
    let newSlide = activeSlide + direction;

    // ensures you don't slide past first and last slide
    if (newSlide !== 0 && !(newSlide > sliderChildrenLength)) {
        // udpates progress
        slider.querySelector(
            '.slide__controls__progress__active'
        ).innerHTML = newSlide;

        sliderChildren.forEach(function(item, i) {
            //displays new slide
            if (i === newSlide - 1) {
                item.classList.remove('slider__slide');
                item.classList.add('slider__active-slide');
            } else {
                item.classList.remove('slider__active-slide');
                item.classList.add('slider__slide');
            }
        });
        // disables next & prev buttons when reaching beginning or end of slides
        if (newSlide === 1) {
            slider
                .querySelector('.slider__controls__buttons__prev')
                .classList.add('slider__controls__buttons__disabled');
            slider
                .querySelector('.slider__controls__buttons__prev')
                .setAttribute('disabled', true);
        } else {
            slider
                .querySelector('.slider__controls__buttons__prev')
                .classList.remove('slider__controls__buttons__disabled');
            slider
                .querySelector('.slider__controls__buttons__prev')
                .removeAttribute('disabled');
        }

        if (newSlide === sliderChildrenLength) {
            slider
                .querySelector('.slider__controls__buttons__next')
                .classList.add('slider__controls__buttons__disabled');
            slider
                .querySelector('.slider__controls__buttons__next')
                .setAttribute('disabled', true);
        } else {
            slider
                .querySelector('.slider__controls__buttons__next')
                .classList.remove('slider__controls__buttons__disabled');
            slider
                .querySelector('.slider__controls__buttons__next')
                .removeAttribute('disabled');
        }
    }
}

export default {
    launchFn: initSlider,
    launchQuery: `.${className}`,
};
